<script setup>
    import { watch } from 'vue'
    import { debounce } from 'lodash'
    import IconSpan from '@/components/IconSpan.vue'
    import Icon from '@/components/Icon.vue'
    import { useInPdpInvoiceConfigStore } from '@/stores/in_pdp_invoice'
    import Multiselect from 'vue-multiselect'
    import CheckboxList from '@/components/forms/CheckboxList.vue'

    const filters = defineModel('filters')
    const emit = defineEmits(['filter'])

    // Debounce the filter application to avoid too frequent updates
    const debouncedApplyFilters = debounce(() => {
        emit('filter', { ...filters })
    }, 300)

    // Watch for changes in the filters object
    watch(
        filters,
        () => {
            console.log('Filters updated:', filters)
            debouncedApplyFilters()
        },
        { deep: true }
    )

    const configStore = useInPdpInvoiceConfigStore()
    // Note: You may need to add these options to your config store
    const supplierOptions = configStore.getOptions('suppliers')
    const companyOptions = configStore.getOptions('companies')

    const applyFilters = () => {
        debouncedApplyFilters()
    }
    const statusOptions = [
        { label: 'Attribuées', id: 'assigned' },
        { label: 'Non attribuées', id: 'pending' },
        { label: 'Rejetées par la CAE', id: 'rejected' },
        { label: "Rejetées par l'enseigne", id: 'refused' },
    ]
</script>

<template>
    <div class="search_filters">
        <h2 class="collapse_title">
            <IconSpan css-class="icon" name="search"></IconSpan>
            Recherche
            <Icon css="arrow" name="chevron-down"></Icon>
        </h2>
        <div class="collapse_content">
            <form
                class="form-search form-inline"
                @submit.prevent="applyFilters"
            >
                <CheckboxList
                    :options="statusOptions"
                    :model-value="filters.status"
                    label="Statut"
                />
                <div class="form-group">
                    <label for="remote_number">Numéro distant</label>
                    <input
                        id="remote_number"
                        v-model="filters.remote_number"
                        type="text"
                        placeholder="Saisir le numéro distant"
                    />
                </div>

                <div class="form-group">
                    <label for="supplier_ids">Fournisseurs</label>
                    <Multiselect
                        id="supplier_ids"
                        v-model="filters.supplier_ids"
                        :options="supplierOptions"
                        :preserve-search="true"
                        placeholder="Sélectionner"
                        label="label"
                        track-by="id"
                        :show-labels="false"
                        :multiple="true"
                        :close-on-select="false"
                        :clear-on-select="false"
                    >
                    </Multiselect>
                </div>

                <div class="form-group">
                    <label for="company_ids">Sociétés</label>
                    <Multiselect
                        id="company_ids"
                        v-model="filters.company_ids"
                        :options="companyOptions"
                        :preserve-search="true"
                        placeholder="Sélectionner"
                        label="label"
                        track-by="id"
                        :show-labels="false"
                        :multiple="true"
                        :close-on-select="false"
                        :clear-on-select="false"
                    >
                    </Multiselect>
                </div>
            </form>
        </div>
    </div>
</template>
